.source-list .list-group-item{
    font-size: 14px;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    text-align:left;
}

.source-list .list-group-item:hover{
    white-space: normal;
    cursor: pointer;
    background-color: #f5f5f5;
}

.source-list h5{
    cursor: pointer;
}


